@import "~bootstrap/scss/bootstrap-grid";
.home{
  h2{
    font-size: 40px;
    font-family: 'Montserrat', 'sans-serif';
    @include media-breakpoint-down(sm){
      font-size: 28px;
    }
  }
}

.home__mainTopPanel {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  @include media-breakpoint-down(md){
    padding-top: 40px;
    text-align: center;
  }
}

.home__mainTitle {
  margin: 0;
  padding: 0;
  font-weight: bold;
  font-family: 'Montserrat', 'sans-serif';
  color: #184494;
  font-size: 40px;

  span {
    &:last-child {
      font-weight: normal;
    }
  }
  @include media-breakpoint-down(sm){
    font-size: 28px;
  }
}

.home__mainTitleUnderline {
  border-bottom: 3px solid #009fe3;
}

.home__getButtons {
  display: flex;
  flex-direction: row;
  @include media-breakpoint-down(md){
    justify-content: center;
    margin-bottom: 40px;
  }
  @include media-breakpoint-down(sm){
    flex-direction: column;
    justify-content: center;
    margin-bottom: 40px;
  }
}

.home__btnGetCatalogWrap {
  position: relative;
  margin-top: 161px;

  img {
    position: absolute;
    left: 50%;
    height: 96px;
    bottom: calc(100% + 30px);
  }
}

.home__btnGetCatalog {
  width: 240px;
  height: 60px;
  margin-right: 34px;
  border-radius: 3px;
  font-family: 'Montserrat', 'sans-serif';
  color: rgb(255, 255, 255);
  font-size: 18px;
  background-color: #009fe3;
  border: none;
  @include media-breakpoint-down(sm){
    margin: 20px auto;
  }
}

.home__btnalculate {
  width: 240px;
  height: 60px;
  margin-top: 161px;
  border-radius: 3px;
  font-family: 'Montserrat', 'sans-serif';
  color: #009fe3;
  font-size: 18px;
  background-color: transparent;
  position: relative;
  border: 2px solid #009fe3;
  margin-right: 34px;
  @include media-breakpoint-down(sm){
    margin: 20px auto;
  }
}

.home__carousel {
  padding: 0;
  list-style: none;
  width: 680px;
  height: 680px;
  border-radius: 50%;
  background-color: #f5f5f5;
  overflow: hidden;
  position: relative;
  margin: 0 auto;

  .owl-stage-outer, .owl-stage, .owl-item {
    height: 100%;
  }

  .owl-nav {
    button {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);

      &:first-child {
        left: 24px;
      }

      &:last-child {
        right: 24px;
      }
    }
  }
  @include media-breakpoint-down(lg){
    width: 400px;
    height: 400px;
  }
  @include media-breakpoint-down(md){
    width: 450px;
    height: 450px;
  }
  @include media-breakpoint-down(sm){
    width: 80vw;
    height: 80vw;
  }
}


.home__carouselItem {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  > img {
    width: 325px !important;
    margin-bottom: 33px;
  }

  h3 {
    font-size: 16px;
    color: rgb(135, 135, 134);
    font-weight: bold;
    margin: 0;
  }

  p {
    padding-top: 33px;
    font-size: 14px;
    color: rgb(135, 135, 134);
    margin: 0;
  }
  @include media-breakpoint-down(lg){
    >img{
      width: 60% !important;
    }
    h3 {
      font-size: 14px;
    }

    p {
      padding-top: 15px;
      font-size: 10px;
    }
  }
}

.home__carouselItemHeader {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.home__carouselItemContainer {
  width: 225px;
  text-align: left;
}

.home__carouselItemShapes {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 4px;

  img {
    width: 14px !important;
  }
}

.home__carouselCustomDots {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 42px;
  margin-top: -42px;
  position: relative;
  z-index: 2;
  @include media-breakpoint-down(md){
    margin-top: 20px;
  }
}

.home__carouselCustomDotsButton {
  padding: 0;
  margin: 0 8px 0 0;
  opacity: 0.4;
  background-color: transparent;
  border: none;

  &_active {
    opacity: 1;
  }

  &:last-child {
    margin-right: 0;
  }
}

.homeDocumentation {
  background: {
    image: url(../img/home/Photo-48.png);
    position-x: center;
    position-y: center;
    size: cover;
    repeat: no-repeat;
    attachment: initial;
    origin: initial;
    clip: initial;
    color: rgb(24, 68, 148);
  };
  color: #fff;
  padding: 88px 0;
  margin-top: 70px;
}

.homeDocumentation__title {
  margin: 0 0 105px 0;
  padding: 0;
  font-size: 40px;
  font-family: "Montserrat", sans-serif;
  color: rgb(255, 255, 255);
  font-weight: bold;
  text-align: center;
}

.homeDocumentation__block {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  margin-bottom: 37px;

  img {
    margin-right: 12px;
  }

  p {
    margin: 0;
    font-size: 18px;
    color: rgb(248, 248, 248);
  }
  @include media-breakpoint-down(md){
    text-align: center;
    p{
      flex: 1;
    }
  }
}


.homeDocumentation__button {
  border-radius: 3px;
  height: 60px;
  background-color: #009fe3;
  font-size: 18px;
  font-family: "Montserrat", sans-serif;
  color: rgb(255, 255, 255);
  font-weight: bold;
  padding: 0 19px;
  border: none;
  margin-top: 82px;
}

.homeMelt {
  font-size: 18px;
  color: #212121;
  padding: 50px 0;

  p {
    max-width: 400px;
    margin: 47px auto 0;
  }
}

.homeMelt__title {
  font-size: 40px;
  font-family: "Montserrat", sans-serif;
  color: rgb(24, 68, 148);
  font-weight: bold;
  line-height: 1.2;
  text-align: center;
  padding: 0;
  margin: 0 0 47px 0;
}

.homeMelt__imgWrap {
  margin: 0 auto;
  width: 457px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 34px 60px 160px;
  background: #d6d6d6;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.3), -2px -2px 2px rgba(0, 0, 0, 0.1);
  border-left: 1px solid #eeeeee;
  border-top: 1px solid #eeeeee;
  border-right: 1px solid #c2c2c2;
  border-bottom: 1px solid #c2c2c2;
  @include media-breakpoint-down(sm){
    width: 100%;
    padding: 20px 30px 75px;
    img{
      width: 100%;
    }
  }
}

.homeMelt__rightWrap {
  display: flex;
  flex-direction: column;
  height: 100%;
  >img{
    width: 100%;
  }
}

.homeMelt__history {
  display: flex;
  flex: 1;
  align-items: center;

  > div {
    position: relative;
    width: 100%;

    img {
      position: absolute;
      left: 0;
      top: 5px;
    }

    i {
      display: block;
      max-width: 400px;
      margin: 0 auto;
    }
  }
  @include media-breakpoint-down(md){
    >div{
      img{
        display: none;
      }
      i{
        margin: 20px auto 40px;
      }
    }
  }
}

.homeIce {
  background: #f5f5f5;
  padding: 60px 0;
}

.homeIce__title {
  padding: 0;
  margin: 0 0 67px 0;
  text-align: center;
  font-size: 40px;
  font-family: "Montserrat", sans-serif;
  color: rgb(24, 68, 148);
  font-weight: bold;
  line-height: 1.2;
}

.homeIce__desc {
  font-size: 18px;
  color: rgb(33, 33, 33);
  line-height: 1.778;
  text-align: center;
  margin: 0 0 15px 0;
}

.homeIce__brands {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  padding: 45px 0;
  cursor: pointer;
  @include media-breakpoint-down(md){
    display: grid;
    padding-bottom: 0;
    grid-template-columns: 1fr;
    text-align: center;
    grid-gap: 45px;
  }
}

.homeIce__brand {
  position: relative;
}

.homeIce__brandDesc {
  position: absolute;
  display: none;
  left: 50%;
  top: 100%;
  width: 340px;
  border-radius: 3px;
  background: rgba(255, 255, 255, 0.95);
  box-shadow: 0 38px 94.05px 0.95px rgba(61, 61, 61, 0.22);
  z-index: 5;
  text-align: center;
  transform: translateX(-50%);

  p {
    margin: 42px 42px 20px;
    font-size: 18px;
    text-align: left;
  }

  a {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 3px;
    height: 60px;
    padding: 0 30px;
    font-size: 18px;
    font-family: "Montserrat", sans-serif;
    color: rgb(255, 255, 255);
    background: #009fe3;
    text-decoration: none;
    margin-bottom: 27px;
  }
}


.homeIce__brand:focus {
  outline: none;

  .homeIce__brandDesc {
    display: block;
  }
}


.homeComparativeTable {
  background: #009fe3;
  position: relative;
  height: 820px;
  padding: 95px 0 118px;

  .container, .row {
    height: 100%;
  }

  > img {
    position: absolute;
    left: 0;
    top: -40px;
    filter: drop-shadow(2px 4px 8px rgba(0, 0, 0, 0.5));
  }
  @include media-breakpoint-down(md){
    >img{
      display: none;
    }
    text-align: center;
  }
}

.homeComparativeTable__desc {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 100%;

  h2 {
    font-size: 40px;
    font-family: "Montserrat", sans-serif;
    color: rgb(255, 255, 255);
    font-weight: bold;
    line-height: 1.2;
    margin: 0;
  }

  p {
    margin: 0;
    font-size: 18px;
    color: rgb(254, 254, 254);
    line-height: 1.778;
    text-align: left;
    @include media-breakpoint-down(md){
      text-align: center;
    }
  }

  a {
    display: inline-block;
    border: 2px solid rgb(255, 255, 255);
    border-radius: 3px;
    text-decoration: none;
    font-size: 18px;
    font-family: "Montserrat", sans-serif;
    color: rgb(255, 255, 255);
    font-weight: bold;
    line-height: 3.178;
    padding: 0 22px;
  }
}

.homeMachineStructure {
  padding: 84px 0 60px;
  img{
    @include media-breakpoint-down(lg){
      width: 100%;
    }
  }
}

.homeMachineStructure__title {
  font-size: 40px;
  font-family: "Montserrat", sans-serif;
  color: rgb(24, 68, 148);
  font-weight: bold;
  line-height: 1.2;
  text-align: center;
  margin: 0 0 26px;
}

.homeMachineStructure__blocksWrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.homeMachineStructure__block {
  position: relative;
  padding-left: 56px;
  margin-bottom: 20px;

  span {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 0;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: #009fe3;
    font-size: 20px;
    font-family: "Montserrat", sans-serif;
    color: rgb(255, 255, 255);
    font-weight: bold;
    transform: translateY(-25%);
    box-shadow: 0 6px 13px 0 rgba(0, 159, 227, 0.43);
  }

  h4 {
    font-size: 14px;
    color: rgb(24, 68, 148);
    font-weight: bold;
    line-height: 1.571;
    margin: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }
}


.homeNotFreeze {
  padding: 70px 0;
  color: #fff;
  background: {
    image: url(../img/home/stock-photo-hands-of-fans-during-ice-hockey-game-627876656.png);
    position-x: center;
    position-y: center;
    size: cover;
    repeat: no-repeat;
    attachment: initial;
    origin: initial;
    clip: initial;
    color: #184494
  };
}

.homeNotFreeze__title {
  font-size: 40px;
  font-family: "Montserrat", sans-serif;
  color: rgb(255, 255, 255);
  font-weight: bold;
  margin: 0 0 52px;
  text-align: center;
}

.homeNotFreeze__blocksWrap {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.homeNotFreeze__block {
  //display: flex;
  //flex-direction: row;
  //align-items: flex-start;
  position: relative;
  padding: 0 30px 0 70px;
  margin-bottom: 60px;

  img {
    position: absolute;
    right: 100%;
    transform: translateX(50%);
    top: 0;
  }

  h4 {
    font-size: 20px;
    color: rgb(248, 248, 248);
    font-weight: bold;
    margin: 0 0 25px;
  }

  p {
    margin: 0;
    font-size: 18px;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.homeNotFreeze__footer {
  text-align: center;
}

.homeNotFreeze__buttonWrap {
  position: relative;
  margin: 0 auto;
  display: inline-block;

  img {
    position: absolute;
    right: 100%;
    bottom: 50%;
    transform: translate3d(-33px, 15px, 0);
  }
  @include media-breakpoint-down(md){
    img{
      transform: translate3d(-33px, 70px, 0);
    }
  }
  @include media-breakpoint-down(sm){
    img{
      display: none;
    }
  }
}

.homeNotFreeze__button {
  display: inline-block;
  border-radius: 3px;
  background: #009fe3;
  font-family: 'Montserrat', sans-serif;
  font-size: 18px;
  font-weight: bold;
  color: #fff;
  padding: 19px 20px;
  text-decoration: none;
  margin-top: 90px;
  @include media-breakpoint-down(md){
    margin-top: 130px;
  }
}

.homeAntarctica {
  background: #f5f5f5;
  padding: 72px 0 105px;
  @include media-breakpoint-down(md){
    text-align: center;
  }
}

.homeAntarctica__title {
  font-size: 40px;
  font-family: "Montserrat", sans-serif;
  color: rgb(24, 68, 148);
  font-weight: bold;
  text-align: center;
  margin: 0 0 66px;
}

.homeAntarctica__desc {
  font-size: 24px;
  color: rgb(0, 159, 227);
  font-weight: bold;
  line-height: 1.417;
  text-align: left;
  margin: 0;
  @include media-breakpoint-down(md){
    text-align: center;
  }
}

.homeAntarctica__text {
  margin: 0;
  font-size: 18px;
  color: #212121;
  padding-left: 103px;

  &_sub {
    color: #7c7b7b;
    padding-left: 0;
  }

  img {
    display: inline-block;
    clear: both;
    float: left;
    margin-right: 24px;
  }
  @include media-breakpoint-down(md){
    padding-left: 0;
    margin-top: 40px;
  }
}

.homeAntarctica__buttonWrap {
  margin-top: 85px;
  text-align: center;

  a {
    display: inline-block;
    font-size: 18px;
    font-family: "Montserrat", sans-serif;
    color: rgb(255, 255, 255);
    font-weight: bold;
    background: #009fe3;
    text-decoration: none;
    border-radius: 3px;
    padding: 19px 28px;
  }
  @include media-breakpoint-down(md){
    margin-bottom: 40px;
  }
}

.homeAntarcticaPhotosLine {
  height: 320px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  overflow: hidden;
  > div {
    flex: 1 100%;
    display: flex;
    flex-direction: row;
    align-items: flex-start;

    img {
      height: 320px;
      transition: 0.15s ease;
      position: relative;
      z-index: 1;

      &:hover {
        transform: scale(1.1);
        box-shadow: 2px 4px 8px rgba(0, 0, 0, 0.5);
        z-index: 2;
      }
    }
  }
}

.homeSomeAboutUs {
  padding: 80px 0 625px;
  color: #fff;
  overflow: hidden;
  background: {
    image: url(../img/home/Icon.png);
    position-x: center;
    position-y: 140px;
    repeat: no-repeat;
    attachment: initial;
    origin: initial;
    clip: initial;
    color: #184494
  };
}

.homeSomeAboutUs__title {
  text-align: center;
  font-size: 40px;
  font-family: "Montserrat", sans-serif;
  margin: 0 0 87px;
}

.homeSomeAboutUs__desc {
  display: none;
  text-align: center;
  font-size: 18px;
  margin: 0 0 97px;

  &_show {
    display: block;
  }
}

.homeSomeAboutUs__comments {
  border-bottom: 2px solid #163f89;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 40px;
}

.homeSomeAboutUs__comment {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  text-align: center;
  position: relative;

  img {
    cursor: pointer;
    margin-bottom: 38px;
    transition: 0.15s ease;
    height: 110px;
    margin-top: 45px;
    width: unset !important;
  }

  &_active {
    img {
      transform: scale(1.3);
    }
  }
}

.homeSomeAboutUs__commentInfo {
  transition: 0.15s ease;
  opacity: 0;
  display: flex;
  flex-direction: column;
  border-bottom: 2px solid #00d9d6;
  transform: translateY(2px);
  padding-bottom: 45px;
  span{
    white-space: nowrap;
  }
}

.homeSomeAboutUs__comment_active {
  .homeSomeAboutUs__commentInfo {
    opacity: 1;
  }
}

.homeSomeAboutUs__commentInfo {
  span {
    &:first-child {
      font-size: 22px;
      font-weight: bold;
      text-align: center;
      line-height: 1.2;
    }

    &:last-child {
      font-size: 12px;
      font-family: "Montserrat", sans-serif;
      color: rgb(159, 163, 167);
      font-weight: bold;
      text-transform: uppercase;
      line-height: 2.171;
      text-align: center;

    }
  }
}

.homeOurObjects {
  margin-top: -437px;
  padding-bottom: 55px;
}

.homeOurObjects__title {
  font-family: "Montserrat", sans-serif;
  text-align: center;
  font-size: 40px;
  color: rgb(250, 250, 250);
  font-weight: bold;
  margin: 0 0 65px;
}

.homeOurObjects__carousel{
  .owl-dots{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 23px;
    button{
      margin-right: 21px;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background: #d2dde4;
      transition: 0.15s ease;
      &:last-child{
        margin-right: 0;
      }
      &.active{
        transform: scale(1.4);
        background: #009fe3;
      }
    }
  }
}

.homeOurObjects__carouselGrid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 323px 323px;
  @include media-breakpoint-down(md){
    grid-template-columns: 1fr;
  }
}

.homeOurObjects__carouselGridItem {
  display: grid;
  grid-template-columns: 1fr 1fr;
  @include media-breakpoint-down(sm){
    &_left{
      grid-template-columns: 1fr 70%;
    }
    &_right{
      grid-template-columns: 70% 1fr;
    }
  }
}

.homeOurObjects__carouselGridItemImg {
  background: {
    position-x: center;
    position-y: center;
    repeat: no-repeat;
    size: cover;
  };
}

.homeOurObjects__carouselGridItemInfo {
  padding: 40px 40px 33px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  h4 {
    font-size: 22px;
    font-weight: bold;
    margin: 0;
  }

  span {
    font-size: 10px;
    font-family: "Montserrat", sans-serif;
    font-weight: bold;
    text-transform: uppercase;
  }

  p {
    font-size: 14px;
    line-height: 1.857;
    margin: 0;
  }

  a {
    font-size: 14px;
    text-decoration: underline;
    line-height: 1.857;
  }

  @include media-breakpoint-down(sm){
    h4 {
      font-size: 18px;
    }

    span {
      font-size: 8px;
    }

    p {
      font-size: 10px;
      line-height: 1.6;
      margin: 0;
    }

    a {
      font-size: 12px;
      line-height: 1.8;
    }
  }

  &_left {
    text-align: left;
  }

  &_right {
    text-align: right;
    grid-row-start: 1;
  }

  &_white {
    background: #f5f5f5;

    h4 {
      color: rgb(33, 33, 33);
    }

    span {
      color: rgb(159, 163, 167);
    }

    p {
      color: rgb(159, 163, 167);
    }

    a {
      color: rgb(33, 33, 33);
    }
  }

  &_blue {
    background: #009fe3;
    h4 {
      color: #fefefe;
    }

    span {
      color: #ffffff;
    }

    p {
      color: #ffffff;
    }

    a {
      color: #ffffff;
    }
  }

  &_gray {
    background: #d2dde4;
    h4 {
      color: #212121;
    }

    span {
      color: #9fa3a7;
    }

    p {
      color: #9fa3a7;
    }

    a {
      color: #212121;
    }
  }
  &_black {
    background: #212121;
    h4 {
      color: #ffffff;
    }

    span {
      color: #9fa3a7;
    }

    p {
      color: #9fa3a7;
    }

    a {
      color: #ffffff;
    }
  }
}

.homeAddress{
  overflow: hidden;
  position: relative;
  height: 800px;
  h3{
    font-family: 'Montserrat', sans-serif;
    font-size: 40px;
    margin: 50px 0 60px;
    color: #009fe3;
  }
  @include media-breakpoint-down(md){
    height: unset;
  }
}

.homeAddress__quote{
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 60px;
  img{
    margin-right: 10px;
  }
  blockquote{
    margin: 0;
    font-size: 17px;
    color: #4c4c4c;
    padding: 23px 15px;
    background: {
      image: url(../img/home/Rectangle.png);
      position-x: center;
      position-y: center;
      repeat: no-repeat;
      size: contain;
    };
    span{
      display: inline-block;
      font-size: 42px;
      font-family: "Playfair Display", serif;
      color: rgb(0, 159, 227);
      vertical-align: middle;
      transform: translateY(5px);
      margin-right: 4px;
      line-height: 0;
    }
  }
}

.homeAddress__list{
  list-style: none;
  margin: 0 0 100px;
  padding: 0;
}

.homeAddress__listItem{
  font-size: 10px;
  font-family: "Montserrat", sans-serif;
  color: rgb(159, 163, 167);
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 20px;
  &:last-child{
    margin-bottom: 0;
  }
  div{
    padding-top: 8px;
    text-transform: none;
    font-size: 18px;
    font-family: "Open Sans", sans-serif;
    font-weight: bold;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    address, a{
      font-style: normal;
      display: inline-block;
      margin-bottom: 6px;
      text-decoration: none;
      color: rgb(33, 33, 33);
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
}

.homeAddress__socials{
  display: flex;
  flex-direction: row;
  align-items: center;
  a{
    margin-right: 9px;
    display: inline-block;
  }
}

.homeAddress__img{
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 45%;
  background: {
    image: url(../img/home/here.png);
    position-x: center;
    position-y: center;
    repeat: no-repeat;
    size: cover;
  };
  @include media-breakpoint-down(md){
    display: block;
    position: relative;
    width: 100%;
    left: unset;
    top: unset;
    bottom: unset;
    height: 500px;
    margin-top: 40px;
  }
}